import axios from 'axios'
import router from '@/router'
import { BASE_URL, CODE } from '@/utils'

const Axios = axios.create({
  baseURL: BASE_URL,
  ValidityState: () => {
    return status < 500
  }
})

// 请求拦截
Axios.interceptors.request.use((config) => {
  config.headers['token'] = sessionStorage.getItem('token') // 请求头携带token
  return config
}, (error) => {
  return Promise.reject(error)
})

// 响应拦截
Axios.interceptors.response.use((response) => {
  if (response.status === CODE.SUCCESS) { // 请求无误，返回所有数据
    return response
  } else if (response.status === CODE.NO_PERMISSION) { // 请求资源不存在，跳转错误信息提示界面
    return router.push({ path: 'prompt' })
  } else if (response.status === CODE.NOT_FIND) { // 请求资源不存在，跳转"未找到"提示界面
    return router.push({ path: 'notFind' })
  }
}, (error) => {
  return Promise.reject(error)
})

export default Axios



